<template lang="html">
    <div class="sf-option">
        <slot></slot>
    </div>
</template>
<script>
    export default {
        name: 'option',
        props:{
            // 
            value: {
                default: undefined
            },
            //  option  input 
            //  slot 
            title: {
                default: ''
            }
        },
        data() {
            return {
                // 
                selected: false,
                // 
                forSelected: false
            }
        },
        computed: {
            // o input  title title slottext
            optionTitle() {
                return this.title || this.value;

            },
            // value
            optionValue() {
                // if(this.value === undefined) return this.$el.textContent || this.optionTitle || '';
                return this.value;
            }
        },
        mounted() {
            this.$parent.options.push(this);
        },
        beforeDestroy() {
            this.$parent.options.splice(this.$parent.options.indexOf(this), 1);
        }
    }
</script>



