<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li></li>
            <li><a href="#/daily-statis"></a></li>
            <li></li>
        </ul>
        <sf-panel-box title="" class="sf-mv20 sf-mh15">
            <dl dt-col="4">
                <dt>id</dt>
                <dd v-text="item.userId"></dd>
                <dt></dt>
                <dd v-text="item.userName"></dd>
                <dt></dt>
                <dd v-text="item.sex"></dd>
                <dt></dt>
                <dd v-text="item.address"></dd>
                <dt></dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="" class="sf-mv20 sf-mh15">
            <dl>
                <dt>id</dt>
                <dd v-text="item.userId"></dd>
                <dt></dt>
                <dd v-text="item.userName"></dd>
                <dt></dt>
                <dd v-text="item.sex"></dd>
                <dt></dt>
                <dd v-text="item.address"></dd>
                <dt></dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="" class="sf-mv20 sf-mh15">
            <dl class="row">
                <div class="col-4">
                    <dt>id</dt>
                    <dd v-text="item.userId"></dd>
                    <dt></dt>
                    <dd v-text="item.userName"></dd>
                </div>
                <div class="col-4">
                    <dt></dt>
                    <dd v-text="item.sex"></dd>
                    <dt></dt>
                    <dd v-text="item.address"></dd>
                </div>
                <div class="col-4">
                    <dt></dt>
                    <dd>{{item.createTime | moment}}</dd>
                </div>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="" class="sf-mv20 sf-mh15">
            <dl vertical>
                <dt>id</dt>
                <dd v-text="item.userId"></dd>
                <dt></dt>
                <dd v-text="item.userName"></dd>
                <dt></dt>
                <dd v-text="item.sex"></dd>
                <dt></dt>
                <dd v-text="item.address"></dd>
                <dt></dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="" class="sf-mv20 sf-mh15">
            <dl class="row" vertical>
                <div class="col-4">
                    <dt>id</dt>
                    <dd v-text="item.userId"></dd>
                    <dt></dt>
                    <dd v-text="item.userName"></dd>
                </div>
                <div class="col-4">
                    <dt></dt>
                    <dd v-text="item.sex"></dd>
                    <dt></dt>
                    <dd v-text="item.address"></dd>
                </div>
                <div class="col-4">
                    <dt></dt>
                    <dd>{{item.createTime | moment}}</dd>
                </div>
            </dl>
        </sf-panel-box>
        <p class="sf-tc sf-mt40">
            <button type="button" class="sf-button" onclick="history.go(-1)"></button>
        </p>
    </div>
</template>

<script>
    import axios from '../../utils/axios'
    export default {
        name: 'Detail',
        data() {
            return {
                item: {}
            }
        },
        computed: {
            recId() {
                return this.$route.query.id;
            }
        },
        async created() {
            $.ajax({
                url: 'http://localhost:8011/milestone/admin/userDetail',
                type: 'post',
                data: {recId: this.recId}
            }).then(function(res){
                if (res.code == 0) {
                    this.item = res.data;
                } else {
                    this.$toast('', 'danger');
                }
            }.bind(this));
        }
    }
</script>

<style lang="css">
    
</style>



